import { Component, Host, Event, EventEmitter, Prop, State, h, Watch } from '@stencil/core';
import { TranslateService } from '../../services/translate.service';
import { getUserContent } from "@esri/arcgis-rest-portal";
import { UtilService } from '../../services/util.service';
import { ReportService } from '../../services/report.service';
import { StateService } from '../../services/state.service';

@Component({
  tag: 'report-settings',
  styleUrl: 'report-settings.scss',
  shadow: true,
})
export class ReportSettings {
  @Prop() mergeFiles: 'none' | 'nextPage' | 'continuous';
  @Prop() outputFormat: string = 'word';
  @Prop() conflictBehavior: 'rename' | 'replace' | 'fail' = 'rename'; 

  // @Prop() langObj: any;
  @Prop() fileName: string;
  @Watch('fileName')
  onFileNameChange() {
    this.fileNameChangeHandler({
      target: {
        value: this.fileName
      }
    })
  }


  @Prop() visibleElems: string[] = [];
  @Event() fileOptionChange: EventEmitter<any>;
  @Event() fileNameChange: EventEmitter<any>;
  @Event() selectedFolderChange: EventEmitter<any>;
  @Event() selectedFileFormatChange: EventEmitter<any>;
  
  private folders: any[];
  private selectedFolder: any;
  // private langCommon: any;
  @State() translator: any;
  @State() langReport: any;
  @State() helperObj: any;
  @State() featureCount: number;
  @State() printTemplates: any[] = [];
  private reportService: ReportService = ReportService.getService();
  private state: StateService = StateService.getService();

  componentWillLoad() {
    return Promise.resolve(true)
    // .then(() => {
    //   return TranslateService.getService().getTranslate();
    // })
    .then(() => {
      const res = TranslateService.getService().getTranslateSync();
      this.translator = res;
      this.langReport = res?.customPrint;
      this.state.subscribe('locale-data-changed', (data) => {
        this.translator = data;
        this.langReport = data?.customPrint;
      });
      // this.langCommon = res.common;
      this.printTemplates = this.reportService.getHelperObj('printTemplates') || [];
      this.state.subscribe('print-templates-updated', (data) => {
        this.printTemplates = data || [];
      });
      this.helperObj = this.reportService.getHelperObj();
      this.featureCount = this.reportService.getHelperObj('featureCount') || 0;
      this.state.subscribe('portal-info-update', () => {
        this.featureCount = this.reportService.getHelperObj('featureCount') || 0;
      });
      this.state.subscribe('feature-count-updated', () => {
        this.featureCount = this.reportService.getHelperObj('featureCount') || 0;
      });

      return this.getUserFolders();
    }).catch((err) => {
      this.reportService.showError(err);
    })
  }

  /**
   * merge mode change: 'none' | 'nextPage' | 'continuous'
   * @param evt 
   */
  public fileOptChangeHandler(evt) {
    const cachedParam = this.reportService.getParamCache();
    if (evt.detail !== 'split') {
      cachedParam.mergeFiles = evt.detail;
    } else {
      delete cachedParam.mergeFiles;
    }

    this.fileOptionChange.emit(evt.detail);
  }

  /**
   * file name change handler
   * @param evt 
   */
  public fileNameChangeHandler(evt) {
    this.fileName = evt.target.value;
    const outputReportName = this.fileName;

    // Use outputReportName as outputPackageName if it doesn't have any variables and outputPackageName is not specified by user
    // https://devtopia.esri.com/Beijing-R-D-Center/feature-report/issues/252
    if (outputReportName) {
      const placeholders = this.extractPlaceholders(outputReportName);
      if (!(placeholders && placeholders.length)) {
        this.reportService.setParamCache({
          outputPackageName: outputReportName
        });
      }
    }

    this.reportService.setParamCache({
      outputReportName: outputReportName
    });
    this.fileNameChange.emit(this.fileName);
  }

  /**
   * selected folder changed
   * @param evt 
   */
  public selectedFolderChangeHandler() {
    const uploadInfo = this.reportService.getParamCache('uploadInfo') || {
      type: 'arcgis', // 'ArcGIS Online',
      parameters: {
      },
      conflictBehavior: 'rename'
    };
    uploadInfo.parameters.folderId = this.selectedFolder.id === 'all' ? '' : this.selectedFolder.id;
    this.reportService.setParamCache({
      uploadInfo: uploadInfo
    });

    this.selectedFolderChange.emit(this.selectedFolder);
  }
  /**
   * selected format handlder
   * @param evt 
   */
  public outputFormatChangeHandler(evt) {
    this.outputFormat = evt.target.value;
    this.reportService.setParamCache({
      outputFormat: this.outputFormat
    });
    this.selectedFileFormatChange.emit(this.outputFormat)
  }


  /**
   * get user folders
   * @returns 
   */
  public getUserFolders() {
    const utilService: UtilService = UtilService.getService();
    const userName = utilService.getUser()?.username;
    const rootFolderStr = this.langReport?.saveToAGOFolderRoot.replace('${$username}', userName)
    const params = Object.assign({owner: userName}, utilService.getBaseRequestOptions());
    return getUserContent(params).then((res) => {
      this.folders = [];
      const folders = [];
      // add root folder
      folders.push({
        username: userName,
        id: 'all',
        value: 'all',
        title: rootFolderStr, // todo: use i18n string
        label: rootFolderStr,
        created: null
      });
      if (res && res.folders && res.folders.length) {
        res.folders.forEach((folder) => {
          folder['label'] = folder.title;
          folder['value'] = folder.id;
        });
        this.folders = folders.concat(res.folders);
      } else {
        this.folders = folders;
      }
        // if the folder name is the same as the survey title, set it as the default folder
      // this.selectedFolder = this.folders.find((fd) => {
      //   return fd.title === 'Survey-' + this.survey.title;
      // }) || this.folders[0];
      this.selectedFolder =  this.folders[0];
      return this.folders;
    });
  }

    // remove placeholders in output file name expression
  public extractPlaceholders(exp: string) {
    exp = exp || '';

    const matches = exp.match(/\$[^{$]*?{[^}]*?.*?}/g),
      returnedMatches = [];
    if (matches) {
      matches.forEach((match) => {
        // if not duplicate, push it
        if (returnedMatches.indexOf(match) === -1) {
          returnedMatches.push(match);
        }
      });
    }
    return returnedMatches;
  }

  /**
   * confilict behavior changed.
   * @param evt 
   */
  public changeConflictBehavior(evt) {
    this.conflictBehavior = evt.target.value;
    const uploadInfo = this.reportService.getParamCache('uploadInfo') || {
      type: 'arcgis',
      parameters: {
      },
      conflictBehavior: 'rename'
    };
    uploadInfo.conflictBehavior = this.conflictBehavior;
    this.reportService.setParamCache({
      uploadInfo: uploadInfo
    });
  }


  render() {
    return (
      <Host>
        <div class="heading">
          {this.translator?.customPrint?.resultSettingsLabel1}
        </div>
        {this.visibleElems.indexOf('fileOptions') < 0 ?
          null : <file-option-chooser fileOption={this.mergeFiles} onFileOptionChange={(evt) =>{this.fileOptChangeHandler(evt)}}></file-option-chooser>
        }
        

        {/* report name */}
        {this.visibleElems.indexOf('reportName') < 0 ?
          null : 
          <calcite-label>
            {this.translator?.customPrint?.saveToAGOItemName}
            <calcite-input-text placeholder={this.translator?.customPrint?.saveToAGOItemName} value={this.fileName} onCalciteInputTextChange={(evt) => this.fileNameChangeHandler(evt)}></calcite-input-text>
          </calcite-label>
        }

        {this.visibleElems.indexOf('saveToAGSAccount') < 0 ? null :
          <calcite-label>
            <div class="folder-label">
              <span>{this.translator?.customPrint?.saveToAGOFolder}</span>
              <calcite-dropdown width="l" scale="m" close-on-select-disabled type="click" width-scale="l">
                <calcite-button slot="trigger" icon-start="gear" appearance="transparent"></calcite-button>
                <calcite-dropdown-group>
                  <div class="conflict-name-setting">   
                     {/* there are two bugs in the  calcite-radio-button-group component: 1. the disabled attribute won't work when changing it dyanamically. 2. the onCalciteRadioButtonGroupChange event won't return value              */}
                    <calcite-radio-button-group name="conflict-name-options" layout="vertical">
                        <calcite-label layout="inline">
                        {this.translator?.customPrint?.nameConflictOptLabel}        
                          <calcite-icon id="info-icon" icon="question" scale="s"> </calcite-icon>
                          <calcite-tooltip label={this.translator?.customPrint?.nameConflictOptLabel} reference-element="info-icon">
                              <span>{this.translator?.customPrint?.nameConflictOptLabel}</span>
                          </calcite-tooltip>
                        </calcite-label>
                        <calcite-label layout="inline">
                            <calcite-radio-button value="rename" checked={this.conflictBehavior=='rename'} onCalciteRadioButtonChange={(evt) => this.changeConflictBehavior(evt)}></calcite-radio-button>
                            {this.translator?.customPrint?.nameConflictOptRename}
                        </calcite-label>
                        <calcite-label layout="inline">
                            <calcite-radio-button value="replace" checked={this.conflictBehavior=='replace'} onCalciteRadioButtonChange={(evt) => this.changeConflictBehavior(evt)}></calcite-radio-button>
                            {this.translator?.customPrint?.nameConflictOptReplace}
                        </calcite-label>
                        <calcite-label layout="inline">
                            <calcite-radio-button value="fail" checked={this.conflictBehavior=='fail'} onCalciteRadioButtonChange={(evt) => this.changeConflictBehavior(evt)}></calcite-radio-button>
                            {this.translator?.customPrint?.nameConflictOptSkip}
                        </calcite-label>
                    </calcite-radio-button-group>
                  </div>
                </calcite-dropdown-group>
          </calcite-dropdown>
            </div>
            {/* todo: add a tooltip: saveToAGOItemNameTip_i18n */}
            <calcite-tooltip label={this.helperObj.canCreateItem === false ? this.langReport.saveToAGONoCreatePrivilegeTip : ''} reference-element="folder-selector">
              <span>{this.helperObj.canCreateItem === false ? this.langReport.saveToAGONoCreatePrivilegeTip : ''}</span>
            </calcite-tooltip>
            <calcite-combobox id="folder-selector" disabled={this.helperObj.canCreateItem === false} placeholder={this.translator?.customPrint?.saveToAGOFolder} selection-mode="single-persist" clearDisabled="true" maxItems="7" value={this.selectedFolder} onCalciteComboboxChange={() => {this.selectedFolderChangeHandler()}}>
              {(this.folders || []).map((folder) => {
                return <calcite-combobox-item value={folder.value} text-label={folder.label} selected={folder.value===this.selectedFolder?.id}></calcite-combobox-item>;
              })}
            </calcite-combobox>
          </calcite-label>
        }

        {this.visibleElems.indexOf('outputFormat') < 0 ? null :
          <calcite-label>
            {this.translator?.customPrint?.outputFormat}
            <calcite-combobox placeholder={this.translator?.customPrint?.outputFormat} selection-mode="single-persist" clearDisabled="true" value={this.outputFormat} onCalciteComboboxChange={(evt) => this.outputFormatChangeHandler(evt)}>
              <calcite-combobox-item value='docx' selected={this.outputFormat==='docx'} text-label={this.translator?.customPrint?.outputFormatDocx}></calcite-combobox-item>
              <calcite-combobox-item value='pdf' selected={this.outputFormat==='pdf'} text-label={this.translator?.customPrint?.outputFormatPdf}></calcite-combobox-item>
            </calcite-combobox>
          </calcite-label>
        }

        <slot></slot>
      </Host>
    );
  }

}
